
<template>
  <div id="app">
    <div class="container-fluid">
      
        
      <b-form @submit="onClickBtn">
      <div class="row border">
        <div class="col ">
          <label for="appKey">App Key</label>

          <b-form-input required v-model="appKey" placeholder="Enter app_key"></b-form-input>
          <label for="appSecret">App Secret</label>
          <b-form-input required v-model="appSecret" placeholder="Enter app_secret"></b-form-input>
        </div>
        <div class="col ">
          <label for="cid">CID</label>
          <b-form-input required v-model="cid" placeholder="Enter cid"></b-form-input>
          <br>
          <div class="row" style="padding: 10px;">
          <div class="col ">
          <b-button type="submit" variant="primary">Submit</b-button> 
          </div>
          <div class="col ">
          <div v-if="loading" class="loader" style="height: 30px; width: 30px;" />
          </div>
          </div>
        </div>
      </div>
      </b-form>
        <b-alert style="height:400; overflow: auto;" v-if="raw" show variant="danger">
      {{ raw }}
        </b-alert>
      <br>
      <div class="row border" style="padding: 10px;">
        <div class="col" style="height:700px; overflow: hidden;">
        <label for="">Not sale props:</label>
        <div class="col border" style="min-height:380px;max-height:670px; overflow: auto;">
          <ListView
            depth="0"
            :loading="loading"
            :handler="handler"
            :selected="selected"
            :data="data"
            :inputprops="inputprops"
          ></ListView>
        </div>
        </div>
        <div class="col" style="height:700px; overflow: hidden;">
        <label for="">Sale props:</label>
        <div class="col border" style="min-height:380px; max-height:670px; overflow: auto;">
          <ListView
            depth="0"
            :loading="loading"
            :handler="handler"
            :selected="saleselected"
            :data="saledata"
            :inputprops="inputprops"
          ></ListView>
        </div>
        </div>
        <div class="col">
          <div class="row v-20">
          <label for="validatation">Validation</label>
          <textarea disabled :value="validatation" class="form-control h-50" rows="2"></textarea>
          </div>
          <div class="row v-50">
          <label for="dataOutput2">Props combinations:</label>
          <textarea disabled :value="dataOutput2" class="form-control h-50" rows="10"></textarea>
          </div>
          <div class="row">
          <label for="dataOutput">Output code</label>
          <textarea disabled :value="dataOutput" class="form-control h-50" rows="10"></textarea>
          <b-button type="submit" variant="primary">Submit</b-button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>


<script>
import ListView from "./components/ListView.vue";
const R = require("ramda");

const TopClient = require("topsdk");

const parser = (parent, data) =>
  R.compose(
    R.sortBy(R.propEq("must", false)),
    R.sortBy(R.propEq("is_parent", false)),
    R.map((child) => ({
      id: R.toString(R.prop("pid")(child)),
      multi: R.prop("multi")(child),
      must: R.prop("must")(child),
      name: R.prop("name")(child),
      pid: R.toString(parent.id),
      is_parent: !!R.compose(
        R.length,
        R.path(["prop_values", "prop_value"])
      )(child),
      childrend: R.compose(
        R.map(
          R.applySpec({
            is_parent: R.compose(R.defaultTo(false), R.prop("is_parent")),
            id: R.compose(R.toString, R.prop("vid")),
            childrend: R.always([]),
            multi: R.always(R.prop("multi")(child)),
            name: R.prop("name"),
            parent_name: R.always(R.compose(R.toString, R.prop("name"))(child)),
            pid: R.always(R.compose(R.toString, R.prop("pid"))(child)),
          })
        ),

        R.defaultTo([]),
        R.path(["prop_values", "prop_value"])
      )(child),
    })),
    R.defaultTo([]),
    // R.filter(R.propEq("must", true)),
    R.path(["itemprops_get_response", "item_props", "item_prop"])
  )(data);

export default {
  name: "App",
  components: {
    ListView,
  },
  computed: {
    dataOutput() {
      const keys = Object.keys(this.selected);
      let props = [];//"props: ";
      let sku_propperties = [];//"sku_propperties: ";
      let input_pids = [];//"input_pids: ";
      let input_str = [];//"input_str: ";
      let input_custom_cpv = [];//"input_custom_cpv: ";

      console.log('input props: ', this.inputprops);
      keys.forEach((key) => {
        const array = this.selected[key];
        array.forEach((info) => {
          props.push(`${info.pid}:${info.id}`);// = props.concat(`${info.pid}:${info.id};`);
        });
        array.forEach((info) => {
          if(this.salePropIds.includes(info.pid))
          sku_propperties.push(`${info.pid}:${info.id}`);// = sku_propperties.concat(`${info.pid}:${info.id};`);
        });
      });

      Object.keys(this.inputprops).forEach((key) => {
        if(this.inputprops[key] != null && this.inputprops[key] != undefined && this.inputprops[key] != "")
        {
          if(!this.salePropIds.includes(key))
          {
            input_pids.push(key);// = input_pids.concat(`${key},`)
            input_str.push(this.inputprops[key]);// = input_str.concat(`${this.inputprops[key]},`);
          }
          else
          {
            props.push(`${key}:-10001`);// = props.concat(`${key}:-10001;`);
            input_custom_cpv.push(`${key}:-10001:${this.inputprops[key]}`);// = input_custom_cpv.concat(`${key}:-10001:${this.inputprops[key]};`)

            sku_propperties.push(`${key}:-10001`);//sku_propperties.concat(`${key}:-10001;`);
          }
        }
      });

      return `${props.length > 0 ? `props: ${props.join(';')}\r\n` : ""}${
        sku_propperties.length > 0 ? `sku_propperties: ${sku_propperties.join(';')}\r\n` : ""
        }${input_pids.length > 0 ? `input_pids: ${input_pids.join(',')} \r\ninput_str:${input_str.join(',')}\r\n` : ""
          }${input_custom_cpv.length > 0 ? `input_custom_cpv: ${input_custom_cpv.join(';')}\r\n` : ""}`;
    },

    dataOutput2() {
      const keys = Object.keys(this.selected);
      let result = "";
      const inputedPids = [];
      const inputedSalePids = [];
      keys.forEach((key) => {
        const array = this.selected[key];
        array.forEach((info) => {
          inputedPids.push(info.pid);
          if(this.salePropIds.includes(info.pid)) inputedSalePids.push(info.pid);
          result = result.concat(
            this.salePropIds.includes(info.pid) ? 
            ` => "SName": ${info.parent_name},"pid": "${info.pid}" \r\n\t↳ "Name": "${info.name}", "vid": "${info.id}"\r\n`
            :
            ` => "Name": ${info.parent_name},"pid": "${info.pid}" \r\n\t↳ "Name": "${info.name}", "vid": "${info.id}"\r\n`
          );
        });
      });

      Object.keys(this.inputprops).forEach((key) => {
        
        if(this.inputprops[key] != "")
        {
          if(this.salePropIds.includes(key))
          {
            const array = this.saledata.filter(item => item.id == key);
            if(array.length == 0) return;
            const info = array[0];
            inputedSalePids.push(info.id);
            result = result.concat(` => "SCName": ${info.name},"pid": "${info.id}" \r\n\t↳ "${this.inputprops[key]}"\r\n`);
          }
          else
          {
            const array = this.data.filter(item => item.id == key);
            if(array.length == 0) return;
            const info = array[0];
            result = result.concat(` => "Name": ${info.name},"pid": "${info.id}" \r\n\t↳ "${this.inputprops[key]}"\r\n`);
          }
        }
      });
      
      return result;
    },
    validatation() {
      const keys = Object.keys(this.selected);
      const inputedPids = [];
      const inputedSalePids = [];
      
      keys.forEach((key) => {
        const array = this.selected[key];
        array.forEach((info) => {
          inputedPids.push(info.pid);
          if(this.salePropIds.includes(info.pid)) inputedSalePids.push(info.pid);
        });
      });

      Object.keys(this.inputprops).forEach((key) => {
        if(this.inputprops[key] != "")
        {
          if(this.salePropIds.includes(key))
          {
            inputedSalePids.push(key);
          }
          else {
            inputedPids.push(key);
          }
        }
      });

      const requiredProps = Array.isArray(this.data) && this.data.length > 0? `require props: ${this.data.filter(item => item.must).filter(item => !inputedPids.includes(item.id)).map(item => item.name).join(';')}` : "";
      const requiredSaleProps = Array.isArray(this.saledata) && this.saledata.length > 0 ? `\r\nrequire sale props: ${ (this.saledata.filter(item => item.must).length > 0 || inputedSalePids.length > 0) 
                                ? this.saledata.filter(item => !inputedSalePids.includes(item.id)).map(item => item.name).join(';') : ""}` : "";
      return requiredProps.concat(requiredSaleProps);
    },
  },
  asyncComputed: {
    async someCalculation() {},
  },
  methods: {
    async handler(selected, node) {
      const checked = R.includes(node, selected);

      if (checked && node.is_parent) {
        const client = new TopClient(this.appKey, this.appSecret, {
          endpoint:
            "https://13qphhir64.execute-api.ap-northeast-1.amazonaws.com/taobao",
          useValidators: false,
          rawResponse: true,
        });
        this.loading = true;
        const data = await client.execute("taobao.itemprops.get", {
          cid: this.cid,
          child_path: `${node.pid}:${node.id}`,
        });
        const saledata = await client.execute("taobao.itemprops.get", {
          cid: this.cid,
          is_sale_prop: 'true',
          child_path: `${node.pid}:${node.id}`,
        });
        this.loading = false;
        const res = parser(node, data);
        const saleres = parser(node, saledata);
        node.childrend = res;
        node.salechildrend = saleres;
      }
    },
    async inputedprop(inputprop, node) {
      console.log(inputprop);
      console.log(node);
    },
    async onClickBtn(event) {
      event.preventDefault();
      const client = new TopClient(this.appKey, this.appSecret, {
        endpoint:
          "https://13qphhir64.execute-api.ap-northeast-1.amazonaws.com/taobao",
        useValidators: false,
        rawResponse: true,
      });

      (this.data = ""), (this.selected = []), (this.raw = "");

      this.loading = true;
      let data = [];

      try {
        data = await client.execute("taobao.itemprops.get", {
          cid: this.cid,
        });

        const datasale = await client.execute("taobao.itemprops.get", {
          cid: this.cid,
          is_sale_prop: true
        });

        const parsedSaleData = parser({ id: this.cid }, datasale);
        this.salePropIds = parsedSaleData.map(prop => prop.id);
        const tempdata = parser({ id: this.cid }, data);
        console.log(tempdata);
        const parsedData = tempdata.filter(prop => {console.log(prop.id); return !this.salePropIds.includes(prop.id)});
        console.log(parsedData);
        if (parsedData.length < 1) {
          this.raw = data;
        }
        const skeletonSelected = {};
        const skeletonInputprops = {};
        for (const prop of parsedData) {
          if(prop.childrend.length == 0) skeletonInputprops[prop.id] = ""
          else skeletonSelected[prop.pid] = [];
        }
        const skeletonSaleSelected = {};
        for (const prop of parsedSaleData) {
          if(prop.childrend.length == 0) skeletonInputprops[prop.id] = ""
          else skeletonSaleSelected[prop.pid] = [];
        }

        this.selected = skeletonSelected;
        this.saleselected = skeletonSelected;
        this.inputprops = skeletonInputprops;
        this.data = parsedData;
        this.saledata = parsedSaleData;
        console.log(skeletonInputprops);
      } catch (error) {
        console.log(error);
        this.raw = "Server error. Please try again!";
      } finally {
        this.loading = false;
      }
    },
  },
  data() {
    return {
      loading: false,
      cid: "50014102",
      appSecret: "",
      appKey: "",
      data: [],
      saledata: [],
      selected: [],
      saleselected: [],
      salePropIds: [],
      inputprops: [],
      //inputedPids: [],
      //inputedSalePids: [],
      //requiredProps: "",
      //requiredSaleProps: "",
      raw: "",
    };
  },
};
</script>

<style lang="scss">
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  margin-top: 60px;
}

.loader {
  border: 16px solid #f3f3f3; /* Light grey */
  border-top: 16px solid blue;
  border-right: 16px solid green;
  border-bottom: 16px solid red;
  border-radius: 50%;
  width: 80px;
  height: 80px;
  animation: spin 2s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
